/* General Page Styles */
html, body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  height: 100%; /* Ensure the full viewport is used */
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Make sure the app takes up the full viewport height */
}

/* Header */
header {
  background-color: #007acc;
  color: white;
  padding: 10px 0;
  text-align: center;
  flex-shrink: 0; /* Prevent shrinking */
}

header h1 {
  margin: 0;
}

header nav ul {
  list-style: none;
  padding: 0;
}

header nav ul li {
  display: inline;
  margin: 0 10px;
}

header nav ul li a {
  color: white;
  text-decoration: none;
}

/* Main Section */
main {
  padding: 20px;
  text-align: center;
  min-height: calc(100vh - 150px); /* Adjust for header and footer */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box; /* Include padding in height calculation */
  max-width: 1200px; /* Constrain width for readability */
  margin: 0 auto;
}

@media (min-width: 1024px) {
  main {
    padding: 40px; /* Increase padding for larger screens */
    font-size: 1.2em; /* Scale font size */
  }

  button,
  input {
    font-size: 0.7em; /* Make input and button text larger */
    padding: 10px 15px; /* Adjust button padding */
  }
}

@media (min-width: 2000px) {
  main {
    padding: 40px; /* Increase padding for larger screens */
    font-size: 1.8em; /* Scale font size */
  }

  button,
  input {
    font-size: 0.5em; /* Make input and button text larger */
    padding: 10px 15px; /* Adjust button padding */
  }
}

/* Footer */
footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: relative; /* Ensure footer remains part of the flow */
  width: 100%;
  bottom: 0;
}

/* Input and Button Styles */
input {
  margin: 0 5px;
  padding: 5px;
  width: 300px; /* Wider inputs for larger screens */
}

button {
  margin: 0 5px;
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Tooltip */
.about-container {
  position: relative;
  display: inline-block;
}

.about-tooltip {
  display: none;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  color: #000;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 250px;
  z-index: 1000;
}

.about-container:hover .about-tooltip {
  display: block;
}

.about-link {
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 3px;
}

.about-link:hover {
  text-decoration: underline;
}

/* Responsive Styles */
@media (max-width: 768px) {
  header nav ul li {
    display: block; /* Stack nav items vertically */
    margin: 5px 0;
  }

  main {
    padding: 10px; /* Adjust padding for smaller screens */
  }

  .about-tooltip {
    width: 90%; /* Adjust tooltip width for smaller screens */
  }
}

@media (max-width: 480px) {
  header {
    padding: 10px;
  }

  main {
    padding: 5px; /* Smallest padding for smaller devices */
  }

  input, button {
    width: 100%; /* Full-width for small screens */
    margin-bottom: 10px;
  }

  button {
    padding: 10px; /* Make buttons larger for touch devices */
  }
}
